const color = {
  color: '#333333',
}

const background = {
  backgroundColor: null,
  background: null,
}

const margin = {
  marginTop: 0,
  marginRight: 0,
  marginBottom: 0,
  marginLeft: 0,
}

const padding = {
  paddingTop: 0,
  paddingRight: 0,
  paddingBottom: 0,
  paddingLeft: 0,
}

const align = {
  align: 'left', // left | center | right
}

const fontSize = {
  fontSize: 12,
}

const lineHeight = {
  lineHeight: 16,
}

const widgets = {
  // 
  text: {
    id: 'text',
    data: {
      title: '',
    },
    style: Object.assign(
      {},
      color,
      background,
      align,
      fontSize,
      lineHeight,
      margin,
      {
        paddingTop: 10,
        paddingRight: 10,
        paddingBottom: 10,
        paddingLeft: 10,
      }
    ),
  },
  // 
  blank: {
    id: 'blank',
    style: {
      height: 10,
      backgroundColor: null,
    },
  },
  // 
  line: {
    id: 'line',
    style: {
      borderWidth: 1,
      borderColor: '#ccc',
      // : solid | dashed | dotted
      borderStyle: 'solid',
      marginLeft: 0,
      marginRight: 0,
    },
  },
  // 
  searchbar: {
    id: 'searchbar',
    data: {
      placeholder: '',
    },
    style: {
      ...margin,
      // : {square: '', round: ''}
      shape: 'square',
      // : 28~40
      inputHeight: 40,
      // 
      backgroundColor: '#f9f9f9',
      // 
      inputBackgroundColor: '#ffffff',
    },
  },
  // 
  swiper: {
    id: 'swiper',
    data: Array(3).fill({
      // 
      title: '',
      // url
      picUrl: null,
      // 
      functional: {
        id: 'none',
      },
    }),
    style: {
      ...margin,
    },
  },
  // 
  grid: {
    id: 'grid',
    data: Array(3).fill({
      title: '',
      icon: null,
      functional: {
        id: 'none',
      },
    }),
    style: {
      ...margin,
      color: color.color,
      backgroundColor: null,
      // 
      cols: 3,
      iconWidth: 50,
      iconHeight: 50,
    },
  },
  // 
  title: {
    id: 'title',
    data: {
      title: '',
      icon: null,
      moreTitle: '',
      moreIcon: null,
      functional: {
        id: 'none',
      },
    },
    style: {
      ...margin,
      paddingLeft: 10,
      paddingRight: 10,
      // : {spaceBetween: '', center: ''}
      layout: 'spaceBetween',
      height: 44,
      backgroundColor: null,
      fontSize: 14,
      color: '#333',
      moreFontSize: 12,
      moreColor: '#999',
    },
  },
  // 
  pictures: {
    id: 'pictures',
    data: Array(1).fill({
      title: '',
      img: null,
      functional: {
        id: 'none',
      },
    }),
    style: {
      ...margin,
      backgroundColor: null,
      cols: 1,
      // 
      space: 5,
    },
  },
  // 
  toolbar: {
    id: 'toolbar',
    data: {},
    style: {
      ...margin,
      paddingTop: 10,
      paddingRight: 10,
      paddingBottom: 10,
      paddingLeft: 10,
      backgroundColor: null,
      color: '#333',
    },
  },
  // 
  products: {
    id: 'products',
    data: Array(2).fill({
      title: '',
      subtitle: '',
      img: null,
      buyicon: null,
      price: '0.00',
      functional: {
        id: 'none',
      },
    }),
    style: {
      ...margin,
      paddingTop: 0,
      paddingBottom: 0,
      paddingTop: 15,
      paddingRight: 15,
      paddingBottom: 15,
      paddingLeft: 15,
      backgroundColor: null,
      // : {1: 1, 2: 2}
      layout: 1,
      // : {'border': '', 'shadow': '', 'none': '' }
      borderType: 'border',
    },
  },
}

export default widgets
