<template>
  <div class="line-setting">
    <div class="setting-title">
      
      <el-tooltip
        effect="dark"
        placement="bottom"
        content="()"
      >
        <i class="el-icon-question" style="cursor: pointer;"></i>
      </el-tooltip>
    </div>
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="first">
        <form-item label=":">
          <el-radio-group v-model="value.style.borderStyle">
            <el-radio label="solid"></el-radio>
            <el-radio label="dashed"></el-radio>
            <el-radio label="dotted"></el-radio>
          </el-radio-group>
        </form-item>
        <form-item label=":">
          <el-color-picker v-model="value.style.borderColor"></el-color-picker>
        </form-item>
        <form-item label=":">
          <el-input-number
            controls-position="right"
            size="small"
            :min="1"
            :max="5"
            v-model="value.style.borderWidth"
          ></el-input-number>
        </form-item>
        <form-item label=":">
          <el-row class="multi-row">
            <el-col :span="12">
              <span class="multi-row__label"></span>
              <el-input-number
                controls-position="right"
                size="small"
                :min="0"
                :max="100"
                v-model="value.style.marginLeft"
              ></el-input-number>
            </el-col>
            <el-col :span="12">
              <span class="multi-row__label"></span>
              <el-input-number
                controls-position="right"
                size="small"
                :min="0"
                :max="100"
                v-model="value.style.marginRight"
              ></el-input-number>
            </el-col>
          </el-row>
        </form-item>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import mixins from '../mixins'
export default {
  name: 'line-setting',
  mixins: [mixins]
}
</script>

<style lang="less" scoped>
@import '../setting.less';
</style>